import React, {useState, useEffect} from 'react';
import { FaBars } from 'react-icons/fa';
import { IconContext } from 'react-icons/lib';
import { animateScroll as scroll } from 'react-scroll';
import { 
    Nav, 
    NavbarContainer, 
    NavLogo, 
    MobileIcon, 
    NavMenu, 
    NavItem, 
    NavLinks,
    NavBtn,
    NavBtnLink 
} from './NavbarElements';
import activeIcon from '../../img/active.svg';

const Navbar = ({ toggle }) => {
    const [scrollNav, setScrollNav] = useState(false);
    const changeNav = () => {
        if(window.scrollY >= 80){
            setScrollNav(true);
        } else{
            setScrollNav(false);
        }

    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }


    return (
        <>
        <IconContext.Provider value={{ color: '#fff' }}>
        <Nav scrollNav={scrollNav}>
            <NavbarContainer>
                <NavLogo to="/" onClick={toggleHome}>
                    Halloween
                </NavLogo>
                <MobileIcon onClick={toggle}>
                    <FaBars/>
                </MobileIcon>
                <NavMenu>
                    <NavItem>
                        <img src={activeIcon} className="active-icon" alt="icon"/>
                        <NavLinks to="about"
                        smooth={true}
                        duration={200}
                        spy={true}
                        exact='true'
                        offset={-80}
        
                        >Drácula</NavLinks>
                    </NavItem>
                    <NavItem>
                        <img src={activeIcon} className="active-icon" alt="icon"/>
                        <NavLinks to="discover"
                        smooth={true}
                        duration={200}
                        spy={true}
                        exact='true'
                        offset={-80}
                        >Gosth</NavLinks>
                    </NavItem>
                    <NavItem>
                        <img src={activeIcon} className="active-icon" alt="icon"/>
                        <NavLinks to="services"
                        smooth={true}
                        duration={200}
                        spy={true}
                        exact='true'
                        offset={-80}
                        >Franky</NavLinks>
                    </NavItem>
                    <NavItem>
                        <img src={activeIcon} className="active-icon" alt="icon"/>
                        <NavLinks to="sign up"
                        smooth={true}
                        duration={200}
                        spy={true}
                        exact='true'
                        offset={-80}
                        >Sign up</NavLinks>
                    </NavItem>
                </NavMenu>
                <NavBtn>
                    <NavBtnLink to="/signin">Sign in</NavBtnLink>
                </NavBtn>
            </NavbarContainer>
        </Nav>

        </IconContext.Provider>

        </>
    );
};

export default Navbar;
