import React, {useState} from 'react'
import DraculaSection from '../components/dracula-section';
import HeroSection from '../components/hero-section';
import Navbar from '../components/navbar';
import Sidebar from '../components/sidebar';
import { homeObjOne, homeObjThree, homeObjTwo } from '../components/dracula-section/data';
import Footer from '../components/footer';

const Home = () => {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen)
    }



    return (
        <>
        <Sidebar isOpen={isOpen} toggle={toggle}/>
        <Navbar toggle={toggle}/>
        <HeroSection/>
        <DraculaSection {...homeObjOne}/>
        <DraculaSection {...homeObjTwo}/>
        <DraculaSection {...homeObjThree}/>
        <Footer/>
        </>
    )
}

export default Home
